<template> 
<!--  -->   
    <div class="contanier">  
        <home-header></home-header> 
        <div class="wrapper">
            <div class="side"></div>
            <div class="main"> 
                <div>
                    <h2 class="h2" style="margin-bottom:10px;">{{this.$route.query.name}}</h2>
                    <button v-for="(item,index) in bt" class="bt" :class="{'hou':item.cc}" :key="index" @click="dj(index)">{{item.name}}</button>
                    <div class="lb">
                        <dpul :lb="lb" :uid="uid"></dpul>
                    </div>
                    <fy :num='num' :limit='limit' :offset='offset' :uid='uid' :name='name' :mid='mid' :path="path" :gjc="gjc"></fy>
                </div>
            </div>
        </div>
        <foot></foot>
    </div>   
</template>

<style scoped>
    .hou{
        background: #f2fbf2;
    }
</style>

<script>
    import HomeHeader from '../components/HomeHeader'
    import Foot from '../components/Foot'
    import dpul from '../components/dpul'
    import fy from '../components/fy'
    export default{
        components: {  
            HomeHeader,Foot,dpul,fy
        },
        data(){
            return{
                num: 9,
                limit: 5,
                offset:0,
                bt:[{name:"",cc:true},{name:"",cc:false},{name:"",cc:false}],
                lb:[],
                gjc:"",
                uid:-1,
                mid:-1,
                name:"",
                path:"/qbdp"
            }
        },
        methods:{
            //
            dj(i){
                this.bt[i].cc=true;
                switch(i){
                    case (0):
                        this.bt[1].cc=false;
                        this.bt[2].cc=false;
                        this.gjc="";
                        break;
                    case (1):
                        this.bt[0].cc=false;
                        this.bt[2].cc=false;
                        this.gjc="";
                        break;
                    case (2):
                        this.bt[1].cc=false;
                        this.bt[0].cc=false;
                        this.gjc="";
                        break;                      
                }
                this.hh();
            },
            //jsondom
            hh(){
                this.offset=parseInt(this.$route.query.dp);
                this.uid=this.$route.query.uid;
                this.mid=this.$route.query.mid;
                this.name=this.$route.query.name;
                    if(this.offset==0){
                        for(var i=0;i<5;i++){
                             this.$set(this.lb,i,{lj:"",name:"",sj:"2018-09-21",fenshu:"xx45",content:"",hzs:666,xx:true});
                        }
                    }
                    else{
                        this.lb=[];
                        for(var i=0;i<4;i++){
                             this.$set(this.lb,i,{lj:"",name:"",sj:"2018-09-21",fenshu:"xx45",content:"",hzs:666,xx:true});
                        }
                    }
            }
        },
        created(){
            this.hh();
        },
        watch:{
            '$route':'hh'
        }
    }
</script>


