<template>   
<!--  -->
    <div class="contanier" :mid="mid" :uid="uid">   
        <div v-show="xsm">
            <h2 class="lv" :name="name">{{name}}     </h2>
            <button v-for="(item,index) in bt" class="bt" :class="{'hou':item.cc}" :key="index" @click="dj(index)">{{item.name}}</button>
            <div v-if="xsf==1" class="lb">
                <dpul :lb="lb" :uid="uid"></dpul>
                <a href="javascript:;" class="show" @click="gd" v-show="qbdp">>{{gs}}</a>
            </div>
            <div v-else class="wbc"></div>
        </div>
    </div>   
</template>

<style scoped>
.hou{
    background: #f2fbf2;
}
.wbc{
    border: 1px solid #aaffaa;
    background: #f2fbf2;
    border-left: 0;
    border-right: 0;
    text-align: center;
    padding: 4px 0 3px;
    color: #494949;
    margin: 15px 0;
}
</style>

<script>
    import {setCookie,getCookie} from '../../static/js/cookie.js'
    import dpul from './dpul'
    export default{
        components: {  
            dpul
        },
        props: ['name','mid','uid'],
        data(){
            return{
                xsm:true,
                qbdp:false,
                xsf:1,
                gs:"9",
                bt:[{name:"",cc:true},{name:"",cc:false},{name:"",cc:false}],
                lb:[],
                gjc:"",
            }
        },
        methods:{
            //
            dj(i){
                this.bt[i].cc=true;
                switch(i){
                    case (0):
                        this.bt[1].cc=false;
                        this.bt[2].cc=false;
                        this.gjc="";
                        break;
                    case (1):
                        this.bt[0].cc=false;
                        this.bt[2].cc=false;
                        this.gjc="";
                        break;
                    case (2):
                        this.bt[1].cc=false;
                        this.bt[0].cc=false;
                        this.gjc="";
                        break;                      
                }
                this.hh();
            },
            //
            gd(){
                this.$router.push({path:'/qbdp',query:{gjc:'',name:this.name,mid:this.mid,uid:this.uid,dp:0}});
            },
            //
            hh(){
                        this.xsf=1;
                            for(var i=0;i<5;i++){
                                this.$set(this.lb,i,{lj:"",name:"",sj:"2018-09-21",fenshu:"xx45",content:"",hzs:666,xx:true});
                            }
                            this.qbdp=true;
                            this.gs=res.body.data.gs;
                        }

        },
        created(){
            this.hh();
        }
    }
</script>


