<template>
<!--  --> 
<div class="container" :mid="mid" :uid="uid">
	<div class="pinjia" v-if="xs===1">
		<div class="zh" v-for="items in bs">
			<button class="bt"></button><button class="bt"></button><button class="bt"></button>
    		<span class="wdpj">:</span>
    		<ul class="ul">
				<li v-for="(item,index) in gs" :key="index" @mouseover="change(index,gs,bs)" @mouseout="w(gs,bs)" @click="px(index)"><router-link :to="lj" target="nm_iframe"><img :src="item.img" height="15px" width="15px;"></router-link></li>
			</ul>
			<span class="chengdu" v-show="items.showtishi">{{items.tishi}}</span>
		</div>
	</div>
	<div v-else>
		<div class="kg"><span class="time">{{time}}</span>
        <button class="bt" @click="cx"></button>
        <button class="bt" @click="sc"></button></div>
        <span class="wdpj">:</span>
        <div class="zh" v-for="items in bsss">
        	<ul class="ul">
            	<li v-for="(item,index) in hss" :key="index" @click="pxx(index)" @mouseover="change(index,hss,bsss)" @mouseout="a(hss,bsss)"><img :src="item.img" height="15px" width="15px;"></li>
        	</ul>
        	<span class="chengdu" v-show="items.showtishi">{{items.tishi}}</span>
        </div>
        <div class="ytbq">{{ytbq}}</div>
        <div>{{brief}}</div>
	</div>
	<div class="pinju" v-show="hh">
        	<div class="head-kg"><p>{{tjsc}}</p><span class="cha" @click="cha">x</span></div>
        	<div class="zhong">
            	<div class="kx">?():</div>
            	<div class="zh" v-for="items in bss">
                	<ul class="ul">
                    	<li v-for="(item,index) in hs" :key="index" @mouseover="change(index,hs,bss)" @mouseout="a(hs,bss)" @click="px(index)"><img :src="item.img" height="15px" width="15px;"></li>
               		</ul>
                	<span class="chengdu" v-show="items.showtishi">{{items.tishi}}</span>
            	</div>
            	<span class="suo" @click="b">{{sq}}</span>
       	 	</div>
            <div v-if="xsf==1">
        	<div class="xia" v-show="bb">
            	<p>():</p>
            	<input type="text" v-model="biaoqian" @keyup="ky"><br>
            	<div class="bq"><span>:</span></div>
            	<div class="wd">
                	<ul class="wdbq" :bq="bq">
                    	<li class="qian" v-for="(item,index) in bq" :class="{'hou':item.cc}" :key="index" @click="dj(index,bq)">{{item.nr}}</li>
                	</ul>
            	</div>
            	<div class="bq"><span>:</span></div>
            	<div class="wd">
                	<ul class="wdbq" :cybq="cybq">
                    	<li class="qian" v-for="(item,index) in cybq" :class="{'hou':item.cc}" :key="index" @click="dj(index,cybq)">{{item.nr}}</li>
               		</ul>
            	</div>   
            	<div class="pl">
                    <span style="float:right;">140</span>
                	<p>:</p>
                	<textarea v-model="duanp" cols="50" rows="2"></textarea>
            	</div>
            	<div class="jin"><input id="jzj" type="checkbox" name=""><span> </span></div>          
        	</div>
            <div class="foot">
                <span>  <input id="fx" type="checkbox" name="fx" checked=""> </span>
                <button id="bc" type="submit" @click="bc"></button>
            </div>
            </div>
            <div v-else>
            <div class="xia" v-show="bb">
                <p></p>
                <input type="text" v-model="biaoti"><br>
                <div class="pl">
                    <span style="float:right;">140</span>
                    <p>:</p>
                    <textarea v-model="changp" cols="50" rows="8" style="font-size:12px;"></textarea>
                </div>
                <div class="jin"><input id="jzj" type="checkbox" name=""><span> </span></div> 
            </div>
        	<div class="foot">
            	<span>  <input id="fx" type="checkbox" name="fx" checked=""> </span>
            	<button id="bc" type="submit" @click="bccp"></button>
        	</div>
            </div>
    </div>
    <div class="xyp">
        <ul class="dpyp">
            <li><img src="../../static/img/xx/dp.gif"><a href="javascript:;" @click="xdp"></a></li><li><img src="../../static/img/xx/xyp.gif"><a href="javascript:;" @click="xcp"></a></li>
        </ul>
    </div>
</div>
</template>

<style scoped>
    @import "../../static/css/vmli.css";
</style>

<script>
	import {setCookie,getCookie} from '../../static/js/cookie.js'
	export default{
		props: ['cybq','bq','mid','uid'],
		data(){
			return{
				hh:false,
				bb:true,
				cc:false,
				lj:"",
				biaoqian:"",
				duanp:"",
                biaoti:"",
                changp:"",
				sq:" ",
				mk:2,
				ytjh:-1,
				jhh:"",
				xs:1,
                xsf:1,
                tjsc:"",
				bs:[{showtishi:false,tishi:''}],
				bss:[{showtishi:false,tishi:''}],
				gs:[{img:"../../static/img/xx/hui.png"},{img:"../../static/img/xx/hui.png"},{img:"../../static/img/xx/hui.png"},{img:"../../static/img/xx/hui.png"},{img:"../../static/img/xx/hui.png"}],
				hs:[{img:"../../static/img/xx/hui.png"},{img:"../../static/img/xx/hui.png"},{img:"../../static/img/xx/hui.png"},{img:"../../static/img/xx/hui.png"},{img:"../../static/img/xx/hui.png"}],
				hss:[{img:"../../static/img/xx/hui.png"},{img:"../../static/img/xx/hui.png"},{img:"../../static/img/xx/hui.png"},{img:"../../static/img/xx/hui.png"},{img:"../../static/img/xx/hui.png"}],
        		bsss:[{showtishi:false,tishi:""}],
        		ytbq:"",
        		brief:"",
        		time:"",
			}
		},
		methods:{
            //
			change(i,a,b){
				for(var j=0;j<i+1;j++){
					a[j].img="../../static/img/xx/ll.png";
				}
				for(var c=i+1;c<5;c++){
					a[c].img="../../static/img/xx/hui.png";
				}
				switch(i){
        			case (0):
        	  			b[0].showtishi=true;
        	  			b[0].tishi="";
        	  			break;
        			case (1):
        	  			b[0].showtishi=true;
        	  			b[0].tishi="";
        	  			break;
        			case (2):
        	  			b[0].showtishi=true;
        	  			b[0].tishi="";
        	  			break;
        			case (3):
        	  			b[0].showtishi=true;
        	  			b[0].tishi="";
        	  			break;
        			case (4):
        	  			b[0].showtishi=true;
        	  			b[0].tishi="";
        	  			break;       	          	  
        		}
			},
            //
			a(b,c){
				b.forEach(function(a){
                    a.img="../../static/img/xx/hui.png";
                });
                for(var j=0;j<this.ytjh+1;j++){
                	b[j].img="../../static/img/xx/ll.png";
                }
                c[0].showtishi=true;
                c[0].tishi=this.jhh;
			},
            //
			w(b,c){
				b.forEach(function(a){
                    a.img="../../static/img/xx/hui.png";
                });
                c[0].showtishi=false;
			},
            //
			b(){
				if(this.mk%2==0){
					this.bb=false;
					this.sq="() ";
					this.mk=3;
				}else{
					this.bb=true;
					this.sq=" ";
					this.mk=2;
				}
			},
            //
			bc(){
				this.hh=false;
				let data = {'score':this.ytjh,'biaoqian':this.biaoqian,'content':this.duanp,'mid':this.mid,'uid':this.uid};
				this.$http.post('http://localhost/douban/pl/pll.php',data,{emulateJSON:true})
        			.then((res)=>{
          				console.log(res)
          				if(res.body.data.post==1){
            				location.reload();
          				}      
        			});
			},
            //140
            bccp(){
                if(this.biaoti.length==0){
                    alert("");
                }
                else if(this.changp.length<140){
                    alert("140");
                }else{
                    this.hh=false;
                    let data = {'score':this.ytjh,'biaoti':this.biaoti,'content':this.changp,'mid':this.mid,'uid':this.uid};
                    this.$http.post('http://localhost/douban/pl/plcp.php',data,{emulateJSON:true})
                    .then((res)=>{
                        console.log(res)
                        if(res.body.data.post==1){
                            location.reload();
                        }      
                    });
                }
            },
            //
			dj(i,a){
				if(a[i].aa%2==0){
					a[i].cc=true;
					a[i].aa=3;
					this.biaoqian+=a[i].nr+' ';
				}else{
					var xx=a[i].nr+' ';
					a[i].cc=false;
					a[i].aa=2;
					this.biaoqian=this.biaoqian.replace(xx,"");
				}
			},
            //
			ky(){
				var zhi=this.biaoqian;
				this.bq.forEach(function(a){
                    if(zhi.indexOf(a.nr+" ")>=0){
                    	a.cc=true;
                    	a.aa=3;
                    }else{
                    	a.cc=false;
                    	a.aa=2;
                    }
                });
                this.cybq.forEach(function(a){
                    if(zhi.indexOf(a.nr+" ")>=0){
                    	a.cc=true;
                    	a.aa=3;
                    }else{
                    	a.cc=false;
                    	a.aa=2;
                    }
                });
			},
            //
			px(i){
				if(this.hh==false){
					this.hh=true;
				}
				for(var j=0;j<i+1;j++){
					this.hs[j].img="../../static/img/xx/ll.png";
				}
				for(var b=i+1;b<5;b++){
					this.hs[b].img="../../static/img/xx/hui.png";
				}
				switch(i){
        			case (0):
        	  			this.bss[0].showtishi=true;
        	  			this.bss[0].tishi="";
        	  			this.jhh="";
        	  			break;
        			case (1):
        	  			this.bss[0].showtishi=true;
        	  			this.bss[0].tishi="";
        	  			this.jhh="";
        	  			break;
        			case (2):
        	  			this.bss[0].showtishi=true;
        	  			this.bss[0].tishi="";
        	  			this.jhh="";
        	  			break;
        			case (3):
        	  			this.bss[0].showtishi=true;
        	  			this.bss[0].tishi="";
        	  			this.jhh="";
        	  			break;
        			case (4):
        	  			this.bss[0].showtishi=true;
        	  			this.bss[0].tishi="";
        	  			this.jhh="";
        	  			break;       	          	  
        		}
				this.ytjh=i;
			},
            //
			pxx(i){
				this.ytjh=i;
				let data = {'score':this.ytjh,'mid':this.mid,'uid':this.uid};
				this.$http.post('http://localhost/douban/pl/xxx.php',data,{emulateJSON:true})
        			.then((res)=>{
          				console.log(res)
          				if(res.body.data.post==1){
            				location.reload();
          				}      
        			});
			},
            //
			cha(){
				this.hh=false;
			},
            //
			cx(){
				this.hh=true;
                this.tjsc="";
                this.xsf=1;
				this.ky();
			},
            xdp(){
                this.hh=true;
                this.tjsc="";
                this.xsf=1;
                this.ky();
            },
            xcp(){
                this.hh=true;
                this.tjsc="";
                this.xsf=2;
            },
            //
			sc(){
				let data = {'mid':this.mid,'uid':this.uid};
				this.$http.post('http://localhost/douban/pl/scc.php',data,{emulateJSON:true})
        			.then((res)=>{
          				console.log(res)
          				if(res.body.data.post==1){
            				location.reload();
          				}    
        			});
			}
		},
		created(){
        //
			if(!getCookie('username')){
				this.lj="";				
			}else{
				let data = {"mid":this.mid,"uid":this.uid}
      			this.$http.post('http://localhost/douban/pl/sc.php',data,{emulateJSON:true})
        			.then((res)=>{
          				console.log(res)
          				if(res.body.data.post!==0){
            				this.xs=2;
            				this.ytjh=parseInt(res.body.data.xz);          
            				switch(this.ytjh){
              					case (0):
                  					this.jhh="";
                  					break;
              					case (1):
                  					this.jhh="";
                  					break;
              					case (2):
                  					this.jhh="";
                  					break;
              					case (3):
                  					this.jhh="";
                  					break;
              					case (4):
                  					this.jhh="";
                  					break;                      
            				};
            				this.time=res.body.data.sj;
                            if(res.body.data.ytbq==null || res.body.data.ytbq==""){this.ytbq="";}else{
            				this.ytbq=""+res.body.data.ytbq;}
            				this.biaoqian=res.body.data.ytbq+" ";
            				this.brief=res.body.data.brief;
            				this.duanp=res.body.data.brief;
                            this.changp=res.body.data.changp;
                            this.biaoti=res.body.data.biaoti;
            				for(let j=0;j<this.ytjh+1;j++){
              					this.hss[j].img="../../static/img/xx/ll.png";
              					this.hs[j].img="../../static/img/xx/ll.png";
            				}
            				this.bsss[0].showtishi=true;
            				this.bsss[0].tishi=this.jhh; 
            				this.bss[0].showtishi=true;
            				this.bss[0].tishi=this.jhh;        
          				}              
       			});
        	}
		}
	}
</script>


