<template>
<!--  --> 
    <div class="container">
        <div class="wrapper">
            <div class="side">
                <ul class="dlxz">
                    <li>><router-link to="/register"></router-link></li>
                </ul>
            </div>
            <div class="main">
                <h1></h1>
                <div class="biaodan">
                    <div><label></label><input type="text" placeholder="//" v-model="username"></div>
                    <div><label></label><input type="password" placeholder="" v-model="password"></div>
                    <div><label></label><button v-on:click="login" class="btn"></button><span style="font-size: 13px;color: red;" v-show="showtishi">{{tishi}}</span></div>
                </div>
            </div>
        </div>
    </div>
</template>

<style scoped>
    @import "../../static/css/dlzc.css";
</style>

<script>
    import {setCookie,getCookie} from '../../static/js/cookie.js'
    export default{
        data(){
            return{
                showtishi: false,
                tishi: '',
                username: '',
                password: '',
                newusername: '',
                newpassword: ''
            }
        },
        methods:{
            //
            login(){
                if(this.username == "" || this.password == ""){
                    this.showtishi=true
                    this.tishi=""
                }else{                  
                    let data = {'username':this.username,'password':this.password}
                    /**/
                    this.$http.post('http://localhost/douban/dlzc/dl.php',data,{emulateJSON:true})
                    .then((res)=>{
                        console.log(res)
                        if(res.body.data.post==0){
                            this.showtishi = true
                            this.tishi=""
                        }
                        else{
                            this.tishi = ""
                            this.showtishi = true
                            setCookie('username',res.body.data.name,1000*60)
                            setCookie('userid',res.body.data.uid,1000*60)
                            setTimeout(function(){
                                this.$router.go(-1)
                            }.bind(this),1000)
                        }
                    }); 
                }
            }
        },       
        mounted(){
            //cookieusernamecookie
            if(getCookie('username')){
                this.$router.push('/home')
            }
        }   
    }
</script>




