
  <template>   
        <!-- htmljshtml --> 
    <div class="contanier">   
        <home-header></home-header> 
        <iframe id="id_iframe" name="nm_iframe" style="display: none;"></iframe>
        <div class="wrapper">
            <div class="side"></div>
            <div class="main">
              <div class="dybt"><h1>{{name}}</h1><span>({{year}})</span></div>
              <div class="gydy">
                <div class="tp"><a href=""><img :src="img" width="135px" height="190px" style="margin-top:2px;"></a><br><span></span></div>
                <div class="dbpf"><pf :fs="fs" :xx="xx" :rs="rs" :fz="fz" :xsf="xsf"></pf></div>
                <div class="dybj">
                  <vue-li :xb="xb"></vue-li>
                </div>
              </div>
              <vm-li :bq="bq" :cybq="cybq" :mid="mid" :uid="uid"></vm-li>   
              <div class="jqjj"><h2 class="lv">     </h2><p v-for="(item,index) in jj" :key="index">{{item.nr}}</p></div>  
              <dp :name="name" :mid="mid" :uid="uid"></dp>  
              <cp :name="name" :mid="mid" :uid="uid"></cp>         
            </div>
        </div>
    </div>
</template>

<style scoped>
  @import "../../static/css/xq.css";
</style>

<script>
  import {setCookie,getCookie} from '../../static/js/cookie.js'
  import HomeHeader from '../components/HomeHeader' 
  import VueLi from '../components/VueLi'
  import VmLi from '../components/VmLi'
  import pf from '../components/pf'
  import dp from '../components/dp'
  import cp from '../components/cp'
  export default { 
    data(){
      return{
        uid:-1,
        mid:5,
        usename:"",
        name:"",
        year:"2012",
        img:"../../static/img/dy/p1062824805.jpg",
        bq:[{nr:"",cc:false,aa:2},{nr:"",cc:false,aa:2},{nr:"",cc:false,aa:2},{nr:"",cc:false,aa:2},{nr:"",cc:false,aa:2},{nr:"",cc:false,aa:2},{nr:"",cc:false,aa:2},{nr:"",cc:false,aa:2}],
        cybq:[{nr:"",cc:false,aa:2},{nr:"",cc:false,aa:2},{nr:"",cc:false,aa:2},{nr:"",cc:false,aa:2},{nr:"",cc:false,aa:2},{nr:"",cc:false,aa:2},{nr:"",cc:false,aa:2},{nr:"",cc:false,aa:2},{nr:"",cc:false,aa:2},{nr:"",cc:false,aa:2}],
        jj:[{nr:""},{nr:""},{nr:""}],
        xb: [{name:"",nr:[]},{name:"",nr:[]},{name:"",nr:[]},{name:"",nr:[]},{name:"/",nr:[""]},{name:"",nr:[""]},{name:"",nr:["2012-01-06"]},{name:"",nr:["110"]},{name:"",nr:["You Are the Apple of My Eye"]}],
        fs:[{kd:"width:20px",x:"5",bfb:"14%"},{kd:"width:20px",x:"4",bfb:"14%"},{kd:"width:20px",x:"3",bfb:"14%"},{kd:"width:20px",x:"2",bfb:"14%"},{kd:"width:20px",x:"1",bfb:"14%"}],
        xx:"xx40",
        rs:"222",
        fz:"8.8",
        xsf:1,
      }
    },
    components: {  
      HomeHeader,VueLi,VmLi,pf,dp,cp
    },
    methods: {
      a(c,i){
        var dy=c.split(" ");
          for(var b=0;b<dy.length;b++){
            this.xb[i].nr[b]=dy[b];
          }
      },
      b(c){
        var dy=c.split(" ");
        for(var d=0;d<10;d++){
            this.cybq[d].nr=dy[d];
        }
      },
      c(c){
        var dy=c.split("  ");
        for(var d=0;d<3;d++){
            this.jj[d].nr=dy[d];
        }
      }
    },
    created(){
      /*cookieusernamecookie*/
      if(getCookie('username')){
        this.qian=false;
        this.hou=true;
        this.usename=getCookie('username');
        this.uid=getCookie('userid');
      }
      this.a("",0);
      this.a("",1);
      this.a("    ",2);
      this.a("  ",3);
      this.b(" 2011        ");
      this.c("         ");
    }
  }
</script>


