<template>  
<!--  --> 
    <div class="contanier" :mid="mid" :uid="uid">   
        <div v-show="xsm">
            <h2 class="lv" :name="name">{{name}}     </h2>
            <button v-for="(item,index) in bt" class="bt" :class="{'hou':item.cc}" :key="index" @click="dj(index)">{{item.name}}</button>
            <div v-if="xsf==1" class="lb">
                <gdcul :lb="lb" :uid="uid"></gdcul>
                <a href="javascript:;" class="show" @click="gd" v-show="qbdp">>{{gs}}</a>
            </div>
            <div v-else class="wbc"></div>
        </div>
    </div>   
</template>

<style scoped>
.hou{
    background: #f2fbf2;
}
.wbc{
    border: 1px solid #aaffaa;
    background: #f2fbf2;
    border-left: 0;
    border-right: 0;
    text-align: center;
    padding: 4px 0 3px;
    color: #494949;
    margin: 15px 0;
}
</style>

<script>
    import {setCookie,getCookie} from '../../static/js/cookie.js'
    import gdcul from './gdcul'
    export default{
        components: {  
            gdcul
        },
        props: ['name','mid','uid'],
        data(){
            return{
                xsm:true,
                xsf:1,
                qbdp:false,
                gs:"",
                bt:[{name:"",cc:true},{name:"",cc:false},{name:"",cc:false}],
                lb:[],
                gjc:"",
            }
        },
        methods:{
            //
            gd(){
                this.$router.push({path:'/qbcp',query:{gjc:'',name:this.name,mid:this.mid,uid:this.uid,dp:0}});
            },
            //
            dj(i){
                this.bt[i].cc=true;
                switch(i){
                    case (0):
                        this.bt[1].cc=false;
                        this.bt[2].cc=false;
                        this.gjc="";
                        break;
                    case (1):
                        this.bt[0].cc=false;
                        this.bt[2].cc=false;
                        this.gjc="";
                        break;
                    case (2):
                        this.bt[1].cc=false;
                        this.bt[0].cc=false;
                        this.gjc="";
                        break;                      
                }
                this.hh();
            },
            //
            hh(){
                
                        this.xsf=1;
                        for(var i=0;i<1;i++){
                            this.$set(this.lb,i,{lj:"",name:"",sj:"2018-09-21",fenshu:"xx50",content:",2014HTMLcssJavaScriptPHPvuejQueryx",hzs:999,mys:0,biaoti:"",xx:true,fold:true,up:"../../static/img/xx/up.png",down:"../../static/img/xx/down.png"});
                        }
            }
        },
        created(){
            this.hh();
        }
    }
</script>


